.competitorsContainer {
  position: absolute;
  display: flex;
  gap: 3.5vh;
  right: 4.5vh;
}

.competitorItem {
  flex: none;
  order: 1;
  flex-grow: 0;
  margin-right: -4.5vh;
  width: 5vh;
  height: 5vh;
}

.competitorAvatar {
  border: 0.5vh solid rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  width: 100%;
  height: 100%;
}

#allCompetitors {
  position: absolute;
  width: 26vh;
  height: 20vh;
}

#numCompetitors {
  position: absolute;
  margin-right: 3vh;
  right: 20vh;
  font-size: 1.5vh;
}

#participantText {
  text-align: right;
  font-size: 2vh;
}