@keyframes contract {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(0.8);
    }
}

@keyframes expand {
    0% {
        transform: scale(0.8);
    }

    30% {
        transform: scale(1);
    }

    70% {
        transform: scale(0.97);
    }

    100% {
        transform: scale(1);
    }
}

.contract {
    width: 65vh;
    height: 70vh;
    animation: contract 0.5s forwards;
    transform-origin: center center;
}

.expand {
    width: 65vh;
    height: 70vh;
    animation: expand 0.5s forwards;
    transform-origin: center center;
}