#teamContainerAll2 {
    background-image: url('../../../assets/leadersTeam/background3.svg');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    top: 6.5vh;
    height: 57vh;
    width: 55vh;
    margin-left: 4vh;
    position: absolute;
    display: none;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
}

#teamTitle2 {
    text-align: center;
    font-size: 3vh;
    color: white;
    position: absolute;
}

#stepsTeamContainer {
    justify-content: center;
    display: flex;
    gap: 1vh;
    margin-top: 3vh;
    direction: rtl;
    position: absolute;
    margin-bottom: 4vh;
    /* margin: auto; */
    top: 52vh;
}
.currentStep {
    border-radius: 50%;
    width: 1vh;
    height: 1vh;
    background-color: white
}

.otherStep {
    border-radius: 50%;
    width: 1vh;
    height: 1vh;
    background-color: white;
    opacity: 0.5;
}
.slide-to-end2 {
    animation: slide-end 0.8s ease-in-out;
}


.slide-to-begining2 {
    animation: slide-begining 0.8s ease-in-out;
}

@keyframes slide-end {
    from {
        right: 4vh;
    }

    to {
        right: -60vh;
    }
}

@keyframes slide-begining {
    from {
        right: 70vh;
    }

    to {
        right: 4vh;
    }
}