#background {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    z-index: 0;
}

#summerContainerAll {
    position: absolute;
    top: 8vh;
    right: 15%;
    width: 50vh;
    height: 90vh;
    display: flex;
    justify-content: center;
    background-image: url('../../assets/summer/iceCream.png');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

#goalAchievers {
    justify-content: center;
    align-items: center;
    flex-direction: row-reverse;
    margin-top: 20vh;
    position: relative;
    height: 20vh;
}

.summerWinner {
    width: 8vh;
    height: 8vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #ccc;
    border-radius: 50%;
    position: absolute;
}

/* .summerWinner:nth-child(1) {
    bottom: 37%;
    right: 12%
}


.summerWinner:nth-child(2) {
  
    top: 0;
    right: 30%;
}

.summerWinner:nth-child(3) {
    top: 0%;
    left: 30%;
}

.summerWinner:nth-child(4) {
    bottom: 37%;
    left: 12%;
}

.summerWinner:nth-child(5) {
    bottom: 0;
    left: 0;
    /* bottom: 37%;
    left: 12%; 
} 

/* .summerWinner:nth-child(6) {
    bottom: 0;
    left: 0;
} */

.summerWinnerAvatar {
    border-radius: 50%;
    border: 0.5vh solid rgba(255, 255, 255, 0.9);
    width: 7vh;
    height: 7vh;
}

.summerWinnerTrophy {
    margin-top: -1.5vh;
    width: 2.5vh;
    height: 2.5vh;
}

#remainingLeaders {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 9vh;
    height: 53vh;
    bottom: 4vh;
}

.summerLeader {
    width: 5vh;
    height: 5vh;
}

.summerLeaderAvatar {
    border-radius: 50%;
    border: 0.3vh solid rgba(255, 255, 255, 0.9);
    width: 5vh;
    height: 5vh;
}

#goalTextContainer {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #8B6500;
    top: 7vh;

}

#goalTitle {
    margin: 0;
    font-size: 3vh;
    margin-bottom: 1vh;
}

#goalPoints {
    font-size: 4vh;
    font-weight: 600;
    margin: 0;
}
@keyframes grow {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.4);
    }

    100% {
        transform: scale(1);
    }
}

.grow {
    animation: grow 1s forwards;
    transform-origin: center center;
}