#background {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    z-index: 0;
}

#laboratoryContainerAll {
    position: absolute;
    width: 80vh;
    height: 80vh;
    right: 20vh;
    margin-top: 11vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

#laboratoryGoalsContainer {
    position: absolute;
    width: 50vh;
    height: 60vh;
    z-index: 5;
}

@keyframes moveSmokeElement {
    0% {
        transform: rotate(0deg);
    }

    20% {
        transform: rotate(10deg);
        right: 12vh;
        bottom: 50.5vh;
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    40% {
        bottom: 50.8vh;

    }

    60% {
        transform: rotate(-10deg);
        right: 30vh;
        bottom: 49.8vh;
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    75% {
        bottom: 50.7vh;

    }

    90% {
        transform: rotate(0deg);
        right: 20vh;
        bottom: 51vh;
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    100% {
        transform: rotate(0deg);
        bottom: 51vh;
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
}

#smoke {
    position: absolute;
    width: 80vh;
    bottom: 51vh;
    z-index: 4;
    right: 20vh;
    animation: moveSmokeElement 6s linear infinite;

}

.laboratoryGoal {
    width: 17vh;
    height: 7vh;
    border-radius: 1vh;
    display: flex;
    flex-direction: column;
    direction: rtl;
    align-items: flex-start;
    justify-content: center;
    padding-right: 1vh;
}

@keyframes waterTiltAnimation {
    0% {
        transform: rotate(0deg);
    }

    20% {
        transform: rotate(10deg);
        left: -11vh;
        top: -1.9vh;
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    60% {
        transform: rotate(-10deg);
        top: -5vh;
        left: -9.6vh;
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    90% {
        transform: rotate(0deg);
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    100% {
        transform: rotate(0deg);
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
}

#water {
    position: absolute;
    top: -3vh;
    left: -10vh;
    width: 155%;
    height: 125%;
    display: flex;
    justify-content: center;
    align-items: center;
    mask-image: url('../../assets/laboratory/mask.svg');
    animation: waterTiltAnimation 6s linear infinite;
    /* z-index: 2; */
}

#bottomLiquid {
    position: absolute;
    width: 46vh;
    height: auto;
    bottom: 16vh;
    right: 51.2vh;
}

#liquid {
    position: absolute;
    width: 45vh;
    height: auto;
    left: 27vh;
}

#testTube {
    background-image: url('../../assets/laboratory/TestTube.svg');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    width: 50vh;
    height: 75vh;
    position: absolute;
    display: flex;
    justify-content: center;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    animation: moveElement 6s linear infinite;
}

@keyframes moveElement {
    0% {
        transform: translateX(-50%) rotate(0deg);
    }

    20% {
        transform: translateX(-50%) rotate(10deg);
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    60% {
        transform: translateX(-50%) rotate(-10deg);
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    90% {
        transform: translateX(-50%) rotate(0deg);
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    100% {
        transform: translateX(-50%) rotate(0deg);
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
}

#laboratoryGoalText {
    font-size: 1.7vh;
    margin: 0;
}

#laboratoryGoalPoints {
    font-size: 2.5vh;
    margin: 0;
    font-weight: 2vh;
}

@keyframes grow {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.4);
    }

    100% {
        transform: scale(1);
    }
}

.grow {
    animation: grow 1s forwards;
    transform-origin: center center;
}
#salesTextContainer{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    bottom: 10vh;

}
#salesTitle{
    margin: 0;
    font-size: 2.2vh;
    margin-bottom: 1vh;
}
#salesPoints{
    font-size: 3.2vh;
    margin: 0;
}