#spaceshipsContainer {
    width: 100vw;
    height: 100vh;
}

#spaceShips {
    width: 52%;
    bottom: 4vh;
    right: 5vh;
    height: 100%;
    position: absolute;
}

.spaceShip {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    gap: 16px;
    padding: 10px;
    position: absolute;
    height: 50%;
    width: 20%;
    right: 2vh;
    transform: translateY(0);
    transition: transform 3s ease;
    bottom: 0px;
}

.spaceShipVector {
    width: 100%;
}

.rateIndex {
    right: 26%;
}

.divRate {
    margin-top: -43%;
    /* margin-bottom: 4vh; */
}

.lottieWinSpace {
    width: 30%;
    height: 15%;
    position: absolute;
    bottom: 17vh;
    right: 12vh;
    display: none;
}