#planetLeadersContainer {
    position: absolute;
    width: 81vh;
    height: 83vh;
    right: 22vh;
    top: 8vh;
}

.planetLeaderContainer {
    width: 20vh;
    height: 20vh;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.planetLeaderImage {
    position: absolute;
    width: 100%;
    height: 100%;
}

.leaderPlanetDetails {
    position: absolute;
    color: white;
    font-size: 2.2vh;
    bottom: -8vh;
}

.leaderPlanetName {
    margin-bottom: 0.5vh;
    text-align: center;
    font-weight: bold;
}

.leaderPlanetPoints {
    text-align: center;
    margin-top: 0.5vh;
}

.smoke {
    margin-top: -35vh;
    width: 200%;
    top: -1vh;
    z-index: 3;
}

.planetsRing {
    margin-top: -3vh;
    width: 134%;
}

.glamor {
    mix-blend-mode: screen;
}

.number {
    width: 30%;
    position: absolute;
    top: -6vh;
}

.placeNumber {
    position: relative;
    z-index: 4;
    top: -20vh;

}

.firstPlceNumber {
    width: 179%;
    top: -21vh;
}

#planet1 {
    animation: moveDownUp 7s linear infinite;
}

#planet2 {
    animation: moveRightLeft 7s linear infinite;
}

#planet3 {
    animation: moveLeftRight 7s linear infinite;
}

#planet4 {
    animation: moveUpDown 7s linear infinite;
}

#planet5 {
    animation: moveDownUp 7s linear infinite;
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@keyframes moveDownUp {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(20px);
    }
}

@keyframes moveUpDown {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-20px);
    }
}

@keyframes moveRightLeft {

    0%,
    100% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(20px);
    }
}

@keyframes moveLeftRight {

    0%,
    100% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(-20px);
    }
}