
#divContainer {
    background-image: url('../../assets/nextCompetitions/Images/Artboard\ 1\ 2\ \(1\).svg');
    height: 96vh;
}

#nextCompetition {
    text-align: right;
    font-size: calc(100vh / 22);
    margin-bottom: 0;
    color: #66525B;/* TODO:: color based on template */
}

#competitionName {
    text-align: right;
    width: 40vh;
    font-size: calc(100vh / 12);
    margin-top: 0;
    margin-bottom: 2vh;
    color: #66525B;/* TODO:: color based on template */
    line-height: 1; 
}

#competitionData {
    position: absolute;
    top: 25vh;
    left: 30vh;
}
video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    z-index: -1;
}
