.contract,
.expand {
    height: 70vh;
    width: 65vh;
}

@keyframes contract {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(0.8);
    }
}



.contract {
    animation: contract 0.5s forwards;
    transform-origin: center center;
}

.expand {
    animation: expand 0.5s forwards;
    transform-origin: center center;
}

@keyframes expand {
    0% {
        transform: scale(0.8);
    }   

    30% {
        transform: scale(1);
    }

    70% {
        transform: scale(0.97);
    }


    100% {
        transform: scale(1);
    }
}