    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    height: 85vh;
    width: 83.5vh;
    margin-top: 5vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #66525B;
    direction: rtl;
    margin-left: -5vh;
}

