#boxCompetition {
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    left: 6vh;
    top: 11vh;
    height: 74vh;
}

.firstSellMessage {
    width: 62vh;
}

.regularMessage {
    width: 69vh;
}

#competitorDetails {
    width: 25%;
    margin-bottom: 9vh;
    margin-top: 17.5vh;
}

.textMessage {
    font-size: 31px;
    font-weight: 700;
    margin-top: 8px;
    margin-bottom: 0px;
}

#regularAvatar {
    margin-top: 12vh;
}

#newWinnerAvatar{
    margin-top: 12vh;
    width: 15vh;
    height: 15vh;
    border-radius: 50%;
    border: 1vh solid rgba(255, 255, 255, 0.9);
}
#otherPlaceAvatar {
    margin-top: 2vh;
}

#firstSellAvatar {
    margin-top: 14vh;
}

#newSellAvatar {
    margin-top: 3vh;
}
.avatarCompetitionMessage{
    width: 100%;
}

#productDetails {
    width: 68%;
    height: 16%;
    border-radius: 15px;
    background-color: #FFF7F3CC;
}

.flex {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: center;
}

#placeNumber {
    font-size: 30px;
    font-weight: 800;
    color: white;
}

.avatarMessage{
    width: 100%;
}