#winnersContainer {
    background-image: url('../../assets/winners/background.svg');
    position: absolute;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    height: 70vh;
    width: 65vh;
    margin-top: 14vh;
    left: 12vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

#winnersInternalContainer {
    height: 30vh;
    position: absolute;
    display: flex;
    column-gap: 9vh;
    row-gap: 2vh;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: row-reverse;
    justify-content: center; 
}

#winner {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 7vh;
    position: relative;
}

#winnerAvatar {
    width: 7vh;
    border-radius: 50%;
    border: 0.5vh solid rgba(255, 255, 255, 0.9);
}

#winnerTrophy {
    margin-top: -1.5vh;
    width: 2vh;
}

#winnerName {
    color: #66525B;
    font-weight: bold;
    font-size: 2vh;
    margin-top: 1vh;
}