    #cars {
        position: absolute;
        width: 100vw;
        height: 80vh;
        overflow: hidden;
        bottom: 0px;
    }

    .car {
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        gap: 16px;
        padding: 10px;
        position: absolute;
        right: 2vh;
        transform: translateX(0);
        transition: transform 3s ease;
        flex: 0 0 auto;
    }

    .carVector {
        width: 22%;
    }

    .rateIndex {
        right: 10px;
    }

    .lottieWinRace{
        width: 4%;
        height: 15%;
        position: absolute;
        bottom: 10vh;
        right: 10%;
        display:none;
    }