@import '~@fontsource/rubik/400.css';

#competitorPodium {
    display: flex;
    position: absolute;
    width: 54%;
    height: 75%;
    z-index: 2;
    right: 7vh;
    top: -7vh;
}

#firstPlace {
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    animation: moveUpDown 7s linear infinite;
}

#avatarContainerPlace1 {
    width: 100%;
}

.otherPlace {
    width: 33%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: flex-end;
    animation: moveDownUp 7s linear infinite;
}

.podiumPlaceNumber3 {
    width: 15%;
    margin-bottom: -7%;
    z-index: 1;
}

.podiumPlaceNumber1 {
    width: 100%;
    margin-bottom: -50%;
    z-index: 1;
    margin-right: 2vh;
}

.podiumPlaceNumber2 {
    width: 15%;
    margin-bottom: -7%;
    z-index: 1;
}

.firstPlaceImg {
    width: 100%;
}

.otherPlaceImg {
    width: 100%;
}

.competitorDetails {
    margin-top: -5vh;
    z-index: 2;
}

.textPodium {
    color: #FFFFFF;
    font-family: 'Rubik';
    text-align: center;
    font-size: 2.5vh;
    margin: 0px;
}

.competitorName {
    font-weight: bold;
}

#firstPlace .competitorDetails p {
    font-size: 3vh;
}

.avatarContainer {
    position: relative;
    width: 60%;
}

.avatar {
    transform: translate(-121%, 21%);
    position: absolute;
    width: 70%;
    border-radius: 50%;
}

#avatarContainerPlace1 .avatar {
    width: 45%;
    left: 82%;
    bottom: 32%;
}

.smoke {
    position: absolute;
    width: 190%;
    top: -4vh;
    z-index: 3;
}

.smokeOtherPlace {
    width: 140%;
    top: -10vh;
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@keyframes moveUpDown {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-20px);
    }
}

@keyframes moveDownUp {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(20px);
    }
}