@import '~@fontsource/rubik/400.css';

#box {
    background: url('../../assets/userMessage/box.png');
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    left: 6vh;
    top: 11vh;
    height: 74vh;
    width: 69vh;
}

#timeLineMessage {
    right: 6%;
    position: absolute;
    bottom: 3%;
    width: 90%;
}

#imgMessage {
    position: absolute;
    width: 68%;
    height: 36%;
    right: 16%;
    top: 6%;
    border-radius: 35px;
}

#bubble {
    position: absolute;
    height: 20%;
    left: 9%;
    top: 28%;
}

#titleContainer .title {
    top: 40%;
    right: 16%;
}

.text {
    color: #66525BF2;
    direction: rtl;
    font-family: 'Rubik';
}

#titleContainer {
    position: absolute;
    word-wrap: break-word;
    top: 45%;
    width: 69%;
    height: 18%;
    right: 16%;
}

.title {
    font-size: 40px;
}

.paragraph {
    width: 69%;
    height: 18%;
    word-wrap: break-word;
    font-size: 25px;
    position: absolute;
    right: 16%;
    top: 57%;
}

#competitor {
    position: absolute;
    direction: rtl;
    width: 69%;
    height: 8%;
    right: 16%;
    bottom: 12%;
}

#imgCompetitor {
    position: absolute;
    height: 86%;
    width: 10%;
    display: inline;
    border-radius: 50%;
    overflow: hidden;
}

#competitorDetail {
    position: absolute;
    display: inline;
    width: 87%;
    height: 100%;
    left: 0px;
    font-size: 30px;
}

#competitorName {
    font-size: 25px;
    margin: 0px;
    font-weight: 500;
}

#dateMessage {
    margin: 0px;
    font-size: 20px;
}