#teamMemberContainerAll3 {
    height: 40vh;
    width: 50vh;
    display: none;
    flex-wrap: wrap;
    direction: rtl;
    align-content: flex-start;
    margin-top: 13vh;
    position: absolute;
}

.slide-up-to-end3 {
    animation: slide-up 1s ease-in-out;
}

@keyframes slide-up {
    from {
        bottom: 70vh;
    }

    to {
        bottom: 4vh;
    }
}

.slide-down-from-begining3 {
    animation: slide-down 1.2s ease-in-out;
}

@keyframes slide-down {
    from {
        bottom: 4vh;
    }

    to {
        bottom: -60vh;
    }
}

.teamMemberMemberDiv3 {
    width: 33.3333333%;
    height: 33.3333333%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* Center items horizontally */
    justify-content: center;
    /* Center items vertically */
}

#teamMemberMemberAvatar3 {
    width: 7vh;
    height: 7vh;
    border-radius: 50%;
}

.teamMemberMemberName3 {
    height: 1vh;
    margin: 0;
    margin-top: 1vh;
    margin-bottom: 1vh;
    color: #66525B;
    font-weight: bold;
    font-size: 1.5vh;
    text-align: center;
}