@import '~@fontsource/rubik/700.css';

#leadersContainer {
    background-image: url('../../assets/leaders/leadersBackground.svg');
    position: absolute;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    height: 70vh;
    width: 65vh;
    margin-top: 14vh;
    left: 12vh;
}

#internalContainer {
    position: relative;
    margin-top: 12vh;
    height: 50vh;
    width: 40vh;
    margin-left: 12.5vh;
}

.lead {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    width: 40vh;
    position: relative;
    margin-bottom: 1.2vh;
    border-bottom: 0.2vh solid rgba(0, 0, 0, 0.1);
    padding-bottom: 1.2vh;
}

#buttomLead {
    border-bottom: 0 solid rgba(0, 0, 0, 0.1);
}

.indexContainer {
    position: absolute;
    top: -0.5vh;
    right: -0.5vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#avatar {
    width: 7vh;
    height: 7vh;
    border-radius: 50%;
    border: 0.5vh solid rgba(255, 255, 255, 0.9);
}

#indexBackground {
    width: 3.5vh;
}

#index {
    font-size: 1.5vh;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    color: white;
}

#leaderName {
    color: #66525B;
    font-weight: bold;
    font-size: 2vh;
    margin: 0 1vw;
}

#points {
    color: #66525B;
    font-weight: bold;
    font-size: 2vh;
    margin-right: 10vw;
    position: absolute;
    left: 0
}

#stepsContainer {
    justify-content: center;
    display: flex;
    gap: 1vh;
    margin-top: 1.6vh;
    direction: rtl;
}

.currentStep {
    border-radius: 50%;
    width: 1vh;
    height: 1vh;
    background-color: white
}

.otherStep {
    border-radius: 50%;
    width: 1vh;
    height: 1vh;
    background-color: white;
    opacity: 0.5;
}