@import '~@fontsource/rubik/400.css';

.divRate {
    display: flex;
    width: 12vh;
    height: 4vh;
    border-radius: 3vh;
    font-weight: bold;
    border: 0.7vh solid rgba(255, 255, 255, 0.9);
    align-items: center;
    direction: rtl;
    margin-bottom: 4vh;
    overflow: hidden;
}

.indexImage {
    height: 3.5vh;
}

.rateName {
    font-size: 1.5vh;
    font-family: 'Rubik';
    font-weight: 800;
    margin-right: 4vh;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.index {
    font-family: 'Rubik';
    font-size: 2vh;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0px;
    color: white;
}

.rateIndexRate {
    display: flex;
    align-items: center;
    position: absolute;
    margin: 0px;
}